body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a {
    color: inherit;
    text-decoration: none;
}

a:visited {
    color: inherit;
}

button {
    border: none;
    outline: none;
}

button:hover {
    cursor: pointer;
}

input {
    outline: none;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

body{font-family:"Exo", sans-serif}.page-template{color:#ffffff;width:100%;min-width:1200px;background:linear-gradient(180deg, #5600c4 0%, #c000c4 100%)}.page-template header{width:1200px}.page-template main{display:flex;justify-content:center;width:1200px;min-height:100vh}

body{font-family:"Exo", sans-serif}.luna-card{font-weight:900;font-size:20px}.luna-card .crown{height:28px;margin-left:-14px}.luna-card .crown img{-webkit-filter:drop-shadow(0px 0px 20px #fff);filter:drop-shadow(0px 0px 20px #fff);transform:rotate(-35deg);width:53.1px;height:53.1px}.luna-card .winner img{box-shadow:0px 0px 50px #fffba6}.luna-card .card{display:flex;margin-bottom:13px;width:199px;height:313px;border-radius:50px}.luna-card .card img{background:pink;border-radius:inherit;width:199px;height:313px}.luna-card .card .card-background{position:absolute;width:inherit;height:inherit;border-radius:inherit;opacity:0;transition:0.3s;background:linear-gradient(0deg, rgba(129,129,129,0.5), rgba(129,129,129,0.5));display:flex;justify-content:center;align-items:center}.luna-card .card .card-background button{transition:0.3s;display:none}.luna-card .card .card-background:hover{opacity:1}.luna-card .card .card-background:hover button{display:inline-block;background:rgba(255,255,255,0.01);border:2px solid #ffffff;box-sizing:border-box;box-shadow:0px 4px 4px rgba(0,0,0,0.25);font-weight:600;font-size:27px;color:#fff}.luna-card .star{display:inline-block;box-sizing:border-box;text-shadow:0px 0px 10px #ffffff}.luna-card .star_filled{display:inline-block;color:#fbf567;text-shadow:0px 0px 10px #ffffff}.luna-card.hot{font-weight:600;font-size:30px}.luna-card.hot .crown{height:45px;margin-left:-28px}.luna-card.hot .crown img{width:86.25px;height:86.25px}.luna-card.hot .card{width:270px;height:423px;margin-bottom:13px;border-radius:70px}.luna-card.hot .card img{width:270px;height:423px}

body{font-family:"Exo", sans-serif}.starline{display:flex;justify-content:center}.starline .line{margin-bottom:29px;height:1px;width:860px;background:#fff;text-align:right}.starline .line span{display:inline-block;position:relative;top:-27px;right:-5px;font-size:34.5px;color:#fbf567;text-shadow:0px 0px 10px #fbf567;transform:matrix(0.96, 0.25, -0.26, 0.97, 0, 0)}.starline .line.main{margin-bottom:59px;width:951px}

body{font-family:"Exo", sans-serif}.home{padding-bottom:60px}.home .section{margin:0 auto;margin-top:78px}.home .section h1{margin:0;margin-bottom:15px;font-weight:bold;font-size:60px;text-align:center;background:-webkit-linear-gradient(#ffc02e, #fff780);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.home .intro{width:900px;height:413px}.home .intro .description{margin-left:-35px;width:545px}.home .intro .description span{text-align:right;width:inherit;display:inline-block;font-weight:900;font-size:40px}.home .intro .description span:last-child{margin-top:71px;font-weight:normal;font-size:18px}.home .hot .vs{display:inline-block;margin:0 59px;font-weight:900;font-size:45px}.home .luna .vs{display:inline-block;margin:0 12px;font-weight:900;font-size:30px}.home .luna .ongoing-tab{text-align:right;margin-bottom:60px}.home .luna .ongoing-tab button{margin-left:25px;font-family:Roboto;color:#fff;padding:15px 33px;font-size:24px;background:rgba(255,255,255,0.01);border:2px solid #ffffff;box-sizing:border-box;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:42.5px}.home .luna .ongoing-tab .active{color:#000;font-weight:500;border:none;background:linear-gradient(151.32deg, #fbf567 11.86%, #fff 52.08%, #fbf567 87.56%);box-shadow:0px 4px 4px rgba(0,0,0,0.25)}

body{font-family:"Exo", sans-serif}.header .brand-name{padding-left:75px;padding-top:60px;font-weight:bold;font-size:30px;line-height:40px}.header .menu{padding-top:65px;padding-right:75px;font-size:20px;font-weight:normal}.header .menu>div{margin-left:28px}.header .menu .active{font-weight:bold;border-bottom:1px solid #fff}

body{font-family:"Exo", sans-serif}.intro{width:860px}.intro .title{margin-bottom:12px;font-weight:bold;font-size:45px}.intro .description{font-weight:normal;font-size:25px;text-align:right;padding-right:34px}

body{font-family:"Exo", sans-serif}.login{margin-top:31px}.login .form{margin-bottom:84px}.login .form input{padding:0;margin-top:35px;color:#fff;font-size:20px;width:378px;height:55px;background:none;border:none;border-bottom:1px solid #fff}.login .form input::-webkit-input-placeholder{color:#fff}.login .form input:-ms-input-placeholder{color:#fff}.login .form input::-ms-input-placeholder{color:#fff}.login .form input::placeholder{color:#fff}.login .submit{text-align:center}.login .submit button{font-weight:500;font-size:30px;width:379px;height:90px;color:#000;background:linear-gradient(322.12deg, #fffba6 17.56%, #fff 45.37%, #fffba6 68.9%);box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:100px}.login .join{margin-top:98px;padding-bottom:76px;font-size:18px}.login .join a{display:inline-block;margin-top:18px;text-decoration:underline;text-underline-position:under}

body{font-family:"Exo", sans-serif}.signup{margin-top:31px}.signup .form{margin-bottom:95px}.signup .form .label{display:block;font-weight:600;font-size:30px;padding-top:47px;text-align:left}.signup .form input{color:#fff;font-size:20px;width:378px;height:55px;background:none;border:none;border-bottom:1px solid #fff}.signup .form input::-webkit-input-placeholder{color:#fff}.signup .form input:-ms-input-placeholder{color:#fff}.signup .form input::-ms-input-placeholder{color:#fff}.signup .form input::placeholder{color:#fff}.signup .submit{text-align:center;padding-bottom:75px}.signup .submit button{font-weight:500;font-size:30px;width:507px;height:90px;color:#000;background:linear-gradient(322.12deg, #fffba6 17.56%, #fff 45.37%, #fffba6 68.9%);box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:100px}

